<!-- base -->
<template>
    <div class="chartMain">
        <div v-if="!vhtml" :id="cid"></div>
        <div v-else v-html="vhtml"></div>
    </div>
</template>

<script>
export default {
    name: 'chart-base',
    components: {
    },
    props: {
        pdata: {
            type: Object,
        }
    },
    watch: {
        pdata: {
            deep: true,
            handler(newVal, oldVal) {
                if (newVal.resizeOnly) {
                    this.resize();
                } else {
                    this.cdata = newVal;

                    if (this.myChart && newVal.option) {
                        this.myChart.setOption(newVal.option);
                        this.vhtml = null;
                    }
                    if(newVal.vhtml){
                        this.vhtml = newVal.vhtml;
                    }
                }
            }
        },
    },
    data() {
        return {
            cid: '',
            cdata: {},
            myChart: null,
            vhtml: null,
        };
    },
    methods: {
        resize: function () {
            echarts.init(document.getElementById(this.cid)).resize();
        },
        cavInit: function () {
            let _this = this;
            let chartDom = document.getElementById(this.cid);
            chartDom.style.width = '100%';
            chartDom.style.height = '100%';
            let myChart = echarts.init(chartDom);
            window.addEventListener("resize", function () {
                if (document.getElementById(_this.cid)) {
                    echarts.init(document.getElementById(_this.cid)).resize();
                }
            })

            let option = _this.cdata.option;
            let vhtml = _this.cdata.vhtml;
            
            if(!vhtml){
                option && myChart.setOption(option, true);
                this.myChart = myChart;
                this.vhtml = null;
            }else{
                this.vhtml = vhtml;
            }
        },

    },
    mounted: function () {
        this.$nextTick(function () {
            this.cid = 'base_' + (Math.random() * 1000000);
            this.cdata = this.pdata;

            let _this = this;
            setTimeout(function () {
                _this.cavInit();
            }, 200);
        });
    }
}
</script>
<style scoped>
.chartMain {
    width: 100%;
    height: 100%;
}
</style>



